  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {
    this.pageLoading = false;
  },

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {},

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function (e) {
    //不会重复加载
    //1
   if (this.repeatedLoading) {
		this.repeatedLoading=false;
		this.goodsTabsVoList[this.chooseNavIndex].page = this.goodsTabsVoList[this.chooseNavIndex].page + 1;
		this.getgoodsVo(this.chooseNavIndex);
	}
  },

  /**
   * 用户点击右上角分享
   */
  onShareAppMessage: function () {},
  methods: {
    chooseNav: function (index) {
      this.chooseNavIndex=index;
      this.tabsgoodsVoList=[this.goodsVo1,this.goodsVo2,this.goodsVo3];
      if(!this.tabsgoodsVoList[index].length){
      	this.getgoodsVo(index);
      }
    },

    getgoodsVo(index) {
      var that = this;
      uni.showLoading({
      	title:"加载中...",
		mask:true
      })
      uni.request({
        url: getApp().globalData.url2 + getApp().globalData.getGoodsList + that.goodsTabsVoList[index].page + '/' + that.goodsTabsVoList[index].id,
        method: 'get',
        header: {
          aid: 105
        },
        success: function (res) {
          that.repeatedLoading = true;
          uni.hideLoading()
          if (res.data.code == 200) {
            //判断里面有没有内容
            if (res.data.data.goodsVoList.length > 0) {
              for (var i = 0; i < res.data.data.goodsVoList.length; i++) {
                var img = getApp().globalData.imgUrl + "/" + res.data.data.goodsVoList[i].mainImg;
                res.data.data.goodsVoList[i].mainImg = img;
                var goodsVo1 = that.goodsVo1;
              }

              that.goodsVo1=that.goodsVo1.push(res.data.data.goodsVoList) ;
            } //隐藏导航条加载动画。


            uni.hideNavigationBarLoading();

            uni.stopPullDownRefresh();
          } else {
            uni.showModal({
              title: '提示',
              content: res.data.msg
            });
          }
        }
      });
    },
    commodity_details(e) {
      if (!this.pageLoading) {
        this.pageLoading = true;
        uni.navigateTo({
          url: '/pages/commodity_details/commodity_details?goodsId=' + e.currentTarget.dataset.current
        });
      }
    }

  }
};
